#athena-nav p, a {
  font-family: "Work Sans";
  font-weight: bold;
  color: rgb(0,0,0,0.87);
  font-size: 1em;
}

#athena-nav a:hover {
  border-bottom: 4px solid #F3CD1A;
  color: black;
}

#athena-nav {
  padding-top: 10vh;
  padding-left: 15vw;
  padding-right: 15vw;
  background-color: #F3EAFD;
}

@font-face {
  font-family: 'Work Sans', sans-serif;
  src: url(/static/media/WorkSans-Regular.30be604d.ttf);
  src: url('https://fonts.googleapis.com/css?family=Work+Sans');
}

#about-row {
  background-color: #F3EAFD;
  padding-left: 16vw;
  padding-right: 16vw;
}

#about-h {
  margin-top: 20vh;
  margin-right: -2vw;
}

#athena-hero {
  margin-top: 10vh;
}

#underlined-h:after {
  content: url(/static/media/underline-header.2278658b.svg);
  position: absolute;
  margin-top: 10px;
  margin-left: -135px;
}

@media screen and (max-width: 600px) {
  #about-h {
    margin-top: 15vh;
  }

  #athena-hero {
    margin-top: 0;
    display: none;
  }

  #about-h .athena-primary {
    margin-bottom: 5vh;
  }

}

#pathways-row {
  padding-top: 2vh;
  padding-bottom: 5vh;
  padding-left: 16vw;
  padding-right: 16vw;
}

#pathways-header {
  text-align: center;
  padding-top: 5vh;
}

#pathways-row img {
  padding: 25px;
  height: 240px;
}

#pathways-row .athena-primary {
  font-size: 0.8em;
}

#path-cards p {
  font-size: 0.9em;
  text-align: left;
}

#path-cards {
  padding-top: 5vh;
}

#path-card, #path-card-r {
  height: 420px;
  width: 250px;
  border: 1px solid #F3EAFD;
  display: -webkit-inline-flex;
  display: inline-flex;
  padding-bottom: 5px;
}

#path-card-r {
  margin-right: 4vw;
}

#underlined:after{
  content: url(/static/media/underline-association.924864f3.svg);
  position: absolute;
  margin-top: 14px;
  margin-left: -120px;
}
#underlined-companies:after{
  content: url(/static/media/underline-companies.70e4b48e.svg);
  position: absolute;
  margin-top: 14px;
  margin-left: -105px;
}
#underlined-schools:after{
  content: url(/static/media/underline-schools.fba1cd78.svg);
  position: absolute;
  margin-top: 10px;
  margin-left: -75px;
}

@media screen and (max-width: 600px) {
  #path-card-r {
    margin-right: 0;
    margin-bottom: 1vh;
  }
}

#reason-header {
  text-align: center;
  padding-top: 5vh;
  padding-bottom: 5vh;
}

#reason-row {
  background-color: #F3EAFD;
  width: 100%;
  padding-top: 2vh;
  padding-left: 16vw;
  padding-right: 16vw;
  padding-bottom: 5vh;
}

#top-row, #bottom-row {
  display: -webkit-inline-flex;
  display: inline-flex;
  padding-bottom: 2vh;
}

#bottom-row {
  padding-left: 5vw;
  padding-bottom: 5vh;
}

#stats-img {
  margin-right: 6vw;
}

#stats-img p, #stats-img-sup p {
  font-weight: bold;
  color: rgb(0,0,0,0.87);
}

#stats-img-sup p:after {
  content: url(/static/media/underline-schools.fba1cd78.svg);
  position: absolute;
  margin-top: 14px;
  margin-left: -70px;
}

#stats-img p:after {
  content: url(/static/media/underline-schools.fba1cd78.svg);
  position: absolute;
  margin-top: 14px;
  margin-left: -90px;
}

#reason-footer {
  text-align: center;
}

@media screen and (max-width: 600px) {
  #stats-img-sup {
    clear: left;
  }

  #top-row {
    overflow-x: hidden;
  }

  #stats-img p:after, #stats-img-sup p:after {
    position: relative;
    margin-top: 0;
    margin-left: 0;
  }

}

#footer-row {
  padding-top: 5vh;
  padding-left: 11vw;
  padding-right: 11vw;
}

#footer-row h4 {
  font-size: 0.8em;
}
#footer-row p, ol {
  font-size: 0.6em;
  list-style-type: none;
}

#company-row, #school-row, #association-row {
  padding-top: 5vh;
  padding-left: 22vw;
  padding-right: 22vw;
}

#company-header, #school-header, #association-header {
  padding-top: 15vh;
  padding-left: 52vw;
  height: 30vh;
  background-color: #F3EAFD;
}

#association-header h1, #school-header h1, #company-header h1 {
  margin-top: 5vh;
}

#association-header h1:before {
  content: url(/static/media/association-header.0c34f956.svg);
  position: absolute;
  margin-top: -15vh;
  margin-left: -17vw;
}

#school-header h1:before {
  content: url(/static/media/school-header.ba88906e.svg);
  position: absolute;
  margin-top: -17vh;
  margin-left: -14vw;
}

#company-header h1:before {
  content: url(/static/media/company-header.55bd217b.svg);
  position: absolute;
  margin-top: -17vh;
  margin-left: -12vw;
}

.athena-small-widget {
  height: 32vh;
  width: 25vw;
  background: #FFFFFF;
  border: 1px solid #F3EAFD;
  box-shadow: 0 0 15px 0 #F3EAFD;
  margin-bottom: 2vh;
}

.athena-small-widget p {
  font-size: 0.9em;
  height: 4vh;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.athena-small-widget h3 {
  font-size: 1.5em;
  font-weight: bold;
  height: 8vh;
}

.athena-small-widget .athena-primary {
  font-size: 0.8em;
  margin-bottom: 2vh;
}

.tag-list {
  display: -webkit-inline-flex;
  display: inline-flex;
  height: 3vh;
  width: 100%;
  overflow-x: scroll;
}

.tag-list::-webkit-scrollbar {
  display: none;
}

.badge {
  background-color: #F3EAFD;
  color: rgb(0,0,0,0.54);
  border-radius: 0;
  margin-right: 0.5vw;
}

#athena-load {
  margin-top: 5vh;
  margin-left: 25vw;
}

#loading {
  margin-top: 5vh;
  margin-left: 46vw;
  border-radius: 50% !important;
}

.athena-modal-header, .athena-modal-body, .athena-modal-footer {
  padding-left: 8vw;
  padding-right: 8vw;
}

.athena-modal-header {
  background-color: #F3EAFD;
}

.athena-modal-header h4 {
  font-size: 1.2em;
  font-weight: bold;
  color: rgb(0,0,0,0.8);
}

.athena-modal-body h5 {
  font-size: 1em;
  color: rgb(0,0,0,0.6);
}

.athena-modal-body h4 {
  font-size: 1em;
  font-weight: bold;
  color: rgb(0,0,0,0.8);
  margin-top: 5vh;
}

.athena-modal-body p {
  color: #505051;
}

.athena-modal-footer .athena-primary {
  margin-right: 10vw;
}

.review {
  font-style: italic;
}

@media screen and (max-width: 600px) {
  #association-header h1:before,  #school-header h1:before, #company-header h1:before {
    display: none;
  }

  #company-header, #school-header, #association-header {
    padding-top: 5vh;
    padding-left: 5vw;
    height: 20vh;
    text-align: center;
  }

  #loading {
    margin-left: 35vw;
  }

  .athena-small-widget h3 {
    font-size: 1.2em;
    height: 5vh;
  }

  .athena-small-widget p {
    height: 2vh;
  }

  .athena-small-widget {
    height: auto;
    width: 70vw;
    margin-bottom: 1vh;
  }
}

.sign-up, .sign-in {
  margin-top: 5vh;
  margin-left: 30vw;
  width: 35vw;
  border: solid 1px #F3EAFD;
  padding-top: 2vh;
  padding-left: 4vw;
  padding-right: 4vw;
}

Form {
  font-family: "Work Sans";
  padding-top: 1vh;
  padding-bottom: 5vh;
}

Form p {
  font-family: "Work Sans";
  font-size: 0.8em;
  color: red;
}

.sign-up h5, .sign-in h5 {
  font-family: "Work Sans";
  font-size: 0.8em;
}

.sign-up h2, .sign-in h2 {
  font-family: "Work Sans";
  font-size: 1.5em;
  font-weight: bold;
}

.page-title {
  margin-top: 2%;
  margin-bottom: 2%;
  text-align: center;
}

.athena-form {
  border: 0.5px solid grey;
  padding: 5%;
  margin-top: 2%;
}

.athena-radio {
  display: -webkit-inline-flex;
  display: inline-flex;
  padding-left: 8%;
}

.rating-table {
  padding-top: 5%
}

.athena-question {
  font-weight: bolder;
}

html, body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Work Sans",
    sans-serif;
  font-size: 1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

div, Button, .badge, Input {
  border-radius: 0;
}

.row {
  overflow-x: none;
  margin-right: 0;
}

h1 {
  font-family: "Work Sans";
  font-weight: bold;
  color: rgb(0,0,0,0.87);
  font-size: 2.2em;
}

h2 {
  font-family: "Work Sans";
  color: rgb(0,0,0,0.87);
  font-size: 2.5em;
}

h3 {
  font-family: "Work Sans";
  color: rgb(0,0,0,0.8);
  font-size: 1.2em;
}

h5, p {
  font-family: "Work Sans";
  color: rgb(0,0,0,0.54);
  font-size: 1em;
}

.athena-primary {
  font-family: "Work Sans";
  font-weight: bold;
  background-color: transparent;
  padding: 0;
  border: none;
  color: black;
  text-align:left;
  font-size: 1em;
}

.athena-secondary {
  font-family: "Work Sans";
  font-weight: bold;
  background-color: transparent;
  padding: 0;
  border: none;
  color: #F3CD1A;
  text-align:left;
  font-size: 1em;
}

.athena-primary:hover {
  background-color: #F3CD1A;
  border-radius: 0;
  color: black;
}

